@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins:wght@300;400;500&family=Ubuntu&display=swap);
header{
    height:100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;

}
/******************cta***********/
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap:1.2rem;
    gap:1.2rem;
    justify-content: center;
}
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:0.8rem;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header_socials::after{
    content: "";
    width: 1px;
    height:2rem;
    background: var(--color-primary);
}
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    /* left:35%;    */
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding:5rem 1.5rem 1.5rem 1.5rem;

}
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}
@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
}

@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header_socials,.scroll_down{
        display: none;
    }
}
nav{
    background: rgba(0,0,0,0.3);
    width:-webkit-max-content;
    width:max-content;
    display: block;
    padding:0.7rem,1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap:0.8rem;
    gap:0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display:flex;
    color:var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);    
}

nav a.active{
    background: var(--color-primary);
    color:var(--color-bg)
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap:15%;
    gap:15%;
}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
      
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}
.about__me-image:hover{
    -webkit-transform:rotate(0);
            transform:rotate(0);

}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:1.5rem;
    gap:1.5rem;
}

.about__card{
    background: var(--color-bg-varient);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin:2rem 0 2.6rem;
    color:var(--color-light)
}

/***********Media Queries For Medium devices**************/
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        grid-gap:0;
        gap:0;
    }
    .about__me{
        width:50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

/********************Media Queries For Smaller devices******************/
@media screen and (max-width:600px){
    .about__me{
        width:65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
}
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div{
    background: var(--color-bg-varient);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container >div:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience__details-icon{
    margin-top: 6px;
    color:var(--color-primary);
}
/***********Media Queries:(Medium devices**************/
@media screen and (max-width:1024px){
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container >div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content{
        padding: 1rem;
    }
}

/********************For Smaller devices*/
@media screen and (max-width:600px){
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}

.services__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 3rem;
    gap: 3rem;
}
.service{
    background: var(--color-bg-varient);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}
.service:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.service__head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}
.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list{
    padding: 2rem;
}

.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
}

.service__list p{
    font-size: 0.9rem;
}

/***********Media Queries:(Medium devices**************/
@media screen and (max-width:1024px){
    .services__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}



/********************For Smaller devices*/
@media screen and (max-width:600px){
    .services__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem; 
    }
}
.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-varient);
    background: transparent;
}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3{
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/***********Media Queries:(Medium devices**************/
@media screen and (max-width:1024px){
   .portfolio__container{
    grid-template-columns: 1fr 1fr;
    grid-gap:1.2rem;
    gap:1.2rem;
   }
}

/********************For Smaller devices*/
@media screen and (max-width:600px){
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
   
}

.container.testimonials__container{
    width: 40%;
    padding-bottom: 4rem;
}

.client__avatar{
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-varient);
}

.testimonial{
    background: var(--color-bg-varient);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    -webkit-user-select: none;
            user-select: none;
}
.client__review{
    color:var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;

}
.swiper-pagination-clickable .swiper-pagination-bullet{
    background: var(--color-primary);
}


/***********Media Queries:(Medium devices**************/
@media screen and (max-width:1024px){
   .container.testimonials__container{
    width: 60%;
   }
}



/********************For Smaller devices*/
@media screen and (max-width:600px){
    .container.testimonials__container{
        width: var(--container-width-sm);
    }
    .client__review{
        width: var(--color-light);
    }
    
}
.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
.contact__option{
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}
.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
}
.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}
/*****************FORM*********************************/
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-varient);
    resize: none;
    color: var(--color-white);
}

/***********Media Queries:(Medium devices**************/
@media screen and (max-width:1024px){
   .container.contact__container{
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
   }
}



/********************For Smaller devices*/
@media screen and (max-width:600px){
   .container.contact__container{
    width: var(--container-width-sm);
   }
    
}
footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a{
    color: var(--color-bg);
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display:flex ;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
