    color: var(--color-bg);
}

/***********Media Queries:(Medium devices**************/

 
 
 
 /********************For Smaller devices*/
 @media screen and (max-width:600px){
    .permalinks{
        flex-direction: column;
        grid-gap:1.5rem;
        gap:1.5rem;
    }
